<template>
  <ik-data-table
    ref="table"
    :entity-name="$t('打印单列表')"
    :model="model"
    :show-expand="true"
    :single-expand="true"
    :use-customer-action-only="true"
    :use-default-action="false"
    :use-select="false"
    icon="mdi-clipboard-list"
  >
    <template #expanded-item="{item }">
      <div
        class="text-center"
        v-html="item.content"
      />
    </template>
    <template #footer>
      <v-toolbar
        color="white"
        flat
      />
    </template>
    <template #item.action="{item}">
      <v-icon
        class="mr-2"
        small
        @click="reprint(item)"
      >
        mdi-printer
      </v-icon>
    </template>
  </ik-data-table>
</template>

<script>

  import PrinterList from '../../model/PrinterList'
  import IKUtils from 'innerken-js-utils'
  import { IkDataTable } from 'metaflow-js'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: PrinterList,
      })
    },
    methods: {
      async reprint (item) {
        IKUtils.showLoading()
        await this.model.reprint(item)

        await this.$refs.table.reload()
        IKUtils.toast()
      },
    },
  }
</script>
